import 'rxjs/add/operator/map';
import { Injectable } from '@angular/core';
import {Http} from '@angular/http';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class WpServiceService {

  constructor(private http : Http) { }

  // Get posts data form WP
  getData(cat , count){
    const url = 'https://vineethtr.000webhostapp.com/wp-json/wp/v2/posts?per_page='+count+'&categories='+cat
    return this.http.get(url).map(
      res => {
        const post = res.json();
        return post
      }
    )
  }

  // Get posts data form WP
  getCat(){
    const url = 'https://vineethtr.000webhostapp.com/wp-json/wp/v2/categories'
    return this.http.get(url).map(
      res => {
         const categories = res.json()
        return categories
      }
    )
  }
  // About me
  aboutMe(){
    const url = 'https://vineethtr.000webhostapp.com/wp-json/wp/v2/pages/323'
    return this.http.get(url).map(
      res => {
         const aboutMe = res.json()
        return aboutMe
      }
    )
  }


}



