import { Component, OnInit } from '@angular/core';
import { WpServiceService} from '../wp-service/wp-service.service';

@Component({
  selector: 'work',
  templateUrl: './work.component.html',
  styleUrls: ['./work.component.scss'],
  providers: [WpServiceService]
})
export class WorkComponent implements OnInit {

    public catInfo;

    constructor( private wpService : WpServiceService ) { }
    ngOnInit() {

      // Fetch categories Information
      this.wpService.getCat().subscribe(
        res => {
            this.catInfo = res; // categories Information
        })
    }
}



